import React from "react";
import { Link } from "gatsby";
import ReactMarkdown from 'react-markdown';
import $ from 'jquery';
import Layout from "../../components/layout";
import SEO from "../../components/seo";
import md from "raw-loader!./pixijs-effect.md";
import hljs from "highlight.js";
import 'highlight.js/styles/github.css';

const IndexPage = function(){
    const html = (<ReactMarkdown source={md}/>);
    const post_content = html;
    const highlight = () => {
        try{
            if (typeof document !== 'undefined') {
                $(document).ready(() => {
                    document.querySelectorAll('pre code').forEach((block) => {
                        hljs.highlightBlock(block);
                    });
                });
            }
        } catch(e){ }
    };
    return (<Layout>
              <SEO title="Home" keywords={[`gatsby`, `application`, `react`]} />
              <div className="content_container">
                <div className="inner_container container p-5">
                  <div id="post_container" className="row">
                    <div className="col-md-2">
                    </div>
                    <div className="col-md-8">
                      <h2 className="mt-5 mb-5">Creating a delightful PIXI.js particle effect</h2>
                      {post_content}
                    </div>
                    <div className="col-md-2">
                    </div>
                  </div>
                </div>
              </div>
              {highlight()}
            </Layout>);
};

export default IndexPage;
